import { createSlice } from "@reduxjs/toolkit";
var initialState = {
    tasks: null,
    current: null,
    isFetched: false,
};
var eventTaskSlice = createSlice({
    name: 'eventTask',
    initialState: initialState,
    reducers: {
        setFetchedTasks: function (state, action) {
            var payload = action.payload;
            state.tasks = payload.tasks;
        },
        setCurrentTask: function (state, action) {
            var payload = action.payload;
            state.current = payload.task;
        },
    },
});
export var eventTaskReducer = eventTaskSlice.reducer;
//  actions
export var eventTaskActions = eventTaskSlice.actions;
