import { createSlice } from "@reduxjs/toolkit";
import { APP_TYPE } from "../types/app/app-type";
var initialState = {
    domain: '',
    app: APP_TYPE.EVENT,
    bitrix: {
        user: null,
        company: null,
        deal: null,
        lead: null,
        placement: null
    },
    isResized: false,
    initialized: false,
};
var appSlice = createSlice({
    name: 'app',
    initialState: initialState,
    reducers: {
        setAppData: function (state, action) {
            var payload = action.payload;
            state.domain = payload.domain;
            state.bitrix.placement = payload.placement;
            state.bitrix.user = payload.user;
            console.log('setAppBitrixData');
            console.log(state.domain);
            console.log(state.bitrix.placement);
            console.log(state.bitrix.user);
        },
        setAppBitrixData: function (state, action) {
            var payload = action.payload;
            state.bitrix.company = payload.company;
            state.bitrix.deal = payload.deal;
        },
        setInitializedSuccess: function (state, action) {
            state.initialized = true;
        },
    },
});
export var appReducer = appSlice.reducer;
//  actions
export var appActions = appSlice.actions;
